@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Cards */

.main {
    display: block;
    width: 80%;
    margin: 0 auto;
}

.main .cardBox {
    position: relative;
    width: 100%;
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    gap: 20px;
}

@media screen and (max-width: 947px) {
    .main {
        width: 90%;
    }

    .main .cardBox {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 545px) {
    .main {
        width: 96%;
    }

    .main .cardBox {
        grid-template-columns: repeat(1, 1fr);
    }
}

.main .cardBox .s-card {
    background: #fff;
    position: relative;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    box-shadow: 3px 5px 15px rgba(0, 0, 0, 0.3);
    border-radius: 8px;
}

.cardBox .s-card .numbers {
    font-size: 2.5em;
    font-weight: 600;
    position: relative;
    color: var(--bluish);
}

.cardBox .s-card .cardName {
    color: #777;
    font-weight: 500;
}

.cardBox .s-card .iconBox {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.cardBox .s-card .iconBox i {
    display: block;
    font-size: 2.5em;
    width: 40px;
    height: 40px;
    color: var(--main-green);
    background: rgba(29, 112, 29, 0.2);
    padding: 5px;
    border-radius: 50%;
    text-align: center;

}

.cardBox .s-card .iconBox span {
    display: block;
    font-size: 0.7em;
    color: #777;
    text-align: right;
    margin-top: 5px;
}

.control-label {
    color: #626264;
    font-size: 0.9em;
    font-weight: bold;
}

/* Lower Grid for the dashboard */
.main .main-content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 30px;
    gap: 30px;
    padding: 20px;
}

.main-content .left-main, .main-content .right-main, .main-content .doctors-list {
    display: block;
    position: relative;
    ;
}

.main-content .left-main h2, .main-content .right-main h2 {
    position: relative;
    display: block;
    color: var(--main-green);
    font-weight: 500;
    margin-bottom: 10px;
}

.left-main .doctors-list {
    height: 600px;
    overflow-y: auto;
}

.doctors-list .d-list-item {
    display: flex;
    flex-direction: row;
    padding: 20px;
    border-radius: 7px;
    box-shadow: 0 5px 9px rgba(0,0,0,0.3);
    width: 100%;
    border-right: 6px solid #aaa;
    cursor: pointer;
    transition: 0.3s ease;
    margin-bottom: 15px;
    zoom: 0.8;
}

.doctors-list .d-list-item.active {
    background: rgba(91, 145, 70, 0.123);
    border-right: 6px solid var(--main-green);
}

.doctors-list .d-list-item:hover {
    background: #efefef;
}

.doctors-list .d-list-item .image-part {
    display: block;
    position: relative;
    margin-right: 20px;
}

.doctors-list .d-list-item .image-part img {
    position: relative;
    border-radius: 50%;
    width: 80px;
}

.doctors-list .d-list-item .image-part p {
    color: #999;
    text-align: center;
    position: relative;
    font-weight: bold;
}

.doctors-list .d-list-item .image-part p > span {
    margin-right: 4px;
    font-size: 16px;
}

.doctors-list .d-list-item .name-part {
    display: block;
    position: relative;
    width: 100%;
}

.doctors-list .d-list-item .name-part h3 {
    color: #777;
    font-weight: 600;
    font-size: 24px;
    display: block;
}

.doctors-list .d-list-item .name-part h4 {
    color: var(--main-green);
    font-weight: 400;
    font-size: 15px;
    display: block;
    margin-bottom: 20px;
}

.mt-2 {
    margin-top: 20px;
}

.main-content .left-main input {
    padding-left: 40px;
    position: relative;
    margin-bottom: 20px;
}

.main-content .left-main input:placeholder {
    color: #ddd;
}

.main-content .left-main input ~ i {
    position: absolute;
    content: '\ec81';
    font-family: 'Icofont';
    left: 15px;
    top: 13px;
    color: #ddd;
    z-index: 2;

}

.doctors-list .d-list-item .name-part > .pending {
    float: right;
    display: block;
    background: var(--golden-rod);
    padding: 3px 17px;
    color: #fff;
    outline: none;
    border: 2px solid var(--golden-rod);
    font-weight: 500;
    border-radius: 25px;
    font-size: 14px;
}

.doctors-list .d-list-item .name-part > .approved {
    float: right;
    display: block;
    background: var(--main-green);
    padding: 3px 17px;
    color: #fff;
    outline: none;
    border: 2px solid var(--main-green);
    font-weight: 500;
    border-radius: 25px;
    font-size: 14px;
}

/* Body container for the details part */
.dash-body-container {
    display: grid;
    grid-template-columns: 1fr 2fr;
    font-family: 'Poppins';
    z-index: 4;
    width: 100%;
    margin-left: 30px;
}

@media screen and (max-width: 760px) {
    .dash-body-container {
        grid-template-columns: 1fr;
    }

    .dash-body-container .left-container {
        padding: 30px;
    }
}

.dash-body-container .left-container, .dash-body-container .right-container {
    display: block;
    position: relative;
    background: #fff;
}

.dash-body-container .left-container .profile-image {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.dash-body-container .left-container .profile-image > img {
    width: 100%;
    max-width: 250px;
    display: block;
    position: relative;
    object-fit: cover;
    border-radius: 50%;
    margin: 20px 0;
}

.dash-body-container .left-container .profile-image .title > h2 {
    font-weight: 600;
    font-size: 28px;
    color: var(--bluish);
    text-align: center;
}

.dash-body-container .left-container .profile-image > h3 {
    font-weight: 500;
    font-size: 18px;
    color: #777;
    display: block;
    text-align: center;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

.line-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    border-bottom: 3px solid lightgrey;
    margin-bottom: 40px;
}

.line-container h4 {
    position: absolute;
    background: #efefef;
    color: #777;
    padding: 5px 10px;
    top: -15px;
    left: 0;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 13px;
}


/* Right panel - Body container */
.dash-body-container .right-container {
    display: block;
    position: relative;
    margin-top: 20px;
    padding-left: 10px;
}

.dash-body-container .right-container .title h2 {
    color: var(--main-green);
    font-weight: 400;
    letter-spacing: 1px;
    font-size: 30px;
}

.dash-body-container .right-container .title {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0px;

}

.dash-body-container .right-container .title h4 {
    color: #888;
}

.dash-body-container .right-container > h3 {
    display: block;
    color: var(--bluish);
    font-size: 15px;
}

.right-container .upcoming-appointment {
    display: block;
    position: relative;
    margin: 50px 0 40px 0;
}

.right-container .upcoming-appointment > .title {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-end;
    font-size: 13px;
    color: grey;
}

.right-container .upcoming-appointment .appointment {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.right-container .upcoming-appointment .appointment > img {
    display: inline-block;
    position: relative;
    width: 120px;
}

.right-container .upcoming-appointment .appointment .details {
    display: block;
    color: #444;
}

.right-container .upcoming-appointment .appointment .details h2 {
    margin-bottom: 10px;
}

.right-container .upcoming-appointment .appointment .details h3 {
    margin-bottom: 20px;
    font-weight: normal;
    color: #999;
    font-size: 14px;
}

.right-container .upcoming-appointment .appointment .details h4 {
    margin-bottom: 20px;
    font-weight: normal;
    color: var(--bluish);
}

body, * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.new-loader-bg {
    position: fixed;
    top: 0;
    left: 0;
    display: flex; /*display flex whenever needed to show*/
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    background: #444;
    z-index: 10000;
    overflow: hidden;
    opacity: 0.7;
}

.new-loader-bg .new-loader-container {
    display: flex;
    position: relative;
    width: 350px;
    height: 120px;
    background: transparent;
    flex-direction: row;
    justify-content: space-between;
    -webkit-box-reflect: below -10px linear-gradient(transparent, rgba(0,0,0,0.3));
}

.new-loader-bg .new-loader-container > span {
    display: block;
    position: relative;
    background: rgba(255,255,255,0.3);
    width: 30px;
    height: 90%;
    border-radius: 18px;
    box-shadow: 0 8px 12px rgba(0,0,0,0.6),
                inset 1px 3px 5px rgba(0,0,0,0.2);
    border: 3px solid #fff;
}

.new-loader-bg .new-loader-container > span::before {
    content: '';
    position: absolute;
    display: block;
    border-radius: 50%;
    background: #fff;
    bottom: 0;
    left: -2px;
    border: 1px solid #aff;
    width: 26px;
    height: 26px;
    box-shadow: 0 5px 12px rgba(0,0,0,0.3);
    -webkit-animation: animateNewLoader 1.5s ease-in-out infinite;
            animation: animateNewLoader 1.5s ease-in-out infinite;
    -webkit-animation-delay: calc(0.3s * var(--i));
            animation-delay: calc(0.3s * var(--i));

}

.d-none {
    display: none!important;
}

@-webkit-keyframes animateNewLoader {
    0%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    20% {
        -webkit-transform: translateY(-80px);
                transform: translateY(-80px);
    }
}

@keyframes animateNewLoader {
    0%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    20% {
        -webkit-transform: translateY(-80px);
                transform: translateY(-80px);
    }
}
body {
    font-family: 'Poppins';
}

:root {
    --main-green: #088939;
    --bluish: #0080AE;
    --golden-rod: #a88e16;
    --light-golden-rod: #e4c020;
    --special-blue: #0080AE44;
    --lighter-text: #808080;
    --control-blue: #0080AEAA;
    --lighter-text: #808080;
    --success-bg: rgba(159, 218, 159, 0.6);
}

.header-bar {
    font-family: 'Poppins';
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 90px;
    background: #0080AE;
    background: var(--bluish);
    padding: 0 30px;
    box-shadow: 2px 5px 12px rgba(0, 0, 0, 0.2);
    z-index: 5;
}

.header-bar-image {
    background: transparent;
    color: white;
    font-weight: bold;
    align-items: center;
    justify-content: center;
    display: flex;
}

.header-bar-image>a {
    display: flex;
    align-items: center;
    color: #efefef;
    letter-spacing: 1.5px;
    font-size: 32;
    font-weight: bold;
    text-decoration: none;
}

.header-bar-image>a>img {
    padding: 0 10px;

}

/* Container for login...  */
.container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(135deg, white, white, transparent, #0080AE, #0080AE);
    background: linear-gradient(135deg, white, white, transparent, var(--bluish), var(--bluish));
    z-index: 1;
}

.container .smaller-container {
    display: block;
    width: 75%;
    min-height: 70%;
}

.container .container-divider {
    display: grid;
    grid-template-columns: 3fr 4fr;
    grid-gap: 0;
    gap: 0;
    height: 100%;
}

.container .left-sidebar {
    display: block;
    padding: 40px;
    height: auto;
}

.container .float-logo {
    display: block;
    position: absolute;
    top: calc(50% - 125px);
    left: 50px;
}

.container .float-logo>i {
    position: absolute;
    font-size: 250px;
    opacity: 0.5;
    color: #0080AE;
    color: var(--bluish);
}

.container .left-sidebar .left-text {
    display: block;
    margin-top: 20px;
    position: relative;

}

.container .left-sidebar .left-text>h2 {
    font-weight: 500;
    color: #0080AE;
    font-size: 30px;
    margin-bottom: 20px;
    position: relative;
}

.container .left-sidebar .left-text>h4 {
    font-weight: bold;
    font-family: Poppins;
    margin-bottom: 15px;
    color: #444;
    position: relative;
}

.phone-input-container {
    display: block;
    width: calc(100% - 35px);
    height: 35px;
    margin-bottom: 35px;
    position: relative;
}

.container .left-sidebar .left-text .login-control {
    display: block;
    position: relative;
    outline: none;
    border: 1px solid lightgrey;
    padding: 12px 20px;
    font-size: 18px;
    width: calc(100%);
    border-radius: 3px;

}

.login-control::-webkit-input-placeholder {
    color: rgb(185, 182, 182);
}

.login-control:-ms-input-placeholder {
    color: rgb(185, 182, 182);
}

.login-control::placeholder {
    color: rgb(185, 182, 182);
}

.container .left-sidebar .left-text h5 {
    font-weight: 600;
    color: #888;
    display: block;
    margin-bottom: 7px;
    font-size: 14px;
}

.container .left-sidebar .left-text h5~a {
    /* Terms and conditions */
    display: block;
    position: relative;
    margin-bottom: 50px;
    font-weight: bold;
    color: #0080AE;
}

.container .left-sidebar .left-text>button,
.left-text .btn-continue {
    display: block;
    width: 100%;
    outline: none;
    border: none;
    background: #0080AE;
    color: #FFF;
    padding: 9px 25px;
    text-align: center;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
    margin-bottom: 40px;
    position: relative;
    font-family: Poppins;
}

.btn-continue {
    margin-top: 20px;
}

.container .left-sidebar .doctor-register h4 {
    display: block;
    color: #444;
    position: relative;
    font-size: 14px;
    color: #888;
    font-weight: 500;
}

.container .left-sidebar .doctor-register h4>a {
    display: inline-block;
    position: relative;
    font-weight: 600;
    color: #0080AE;
    margin-left: 7px;
    font-size: 14px;
}

.left-footer {
    display: flex;
    position: absolute;
    bottom: 40px;
    margin: 10px auto;
    align-self: flex-end;
    justify-content: center;
    font-weight: normal;
    color: #444;
}

.left-footer p {
    text-align: center;
}


/* Right sidebar - login */
.container .right-sidebar {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 60px;
    background: #0080AE;
    height: 100%;
    box-shadow: 3px 3px 15px rgba(0, 0, 0, 25%);
}

.container .right-sidebar>img {
    display: block;
    width: 80%;
    margin-bottom: 50px;
}

.container .right-sidebar .image-text {
    display: block;
    position: relative;
}

.container .right-sidebar .image-text>p {
    font-weight: 18px;
    color: #efefef;
    font-size: 35px;
    font-weight: 300;
    text-align: center;
}

/* Input group etc */
.form-data {
    display: block;
    width: 100%;
    position: relative;
    background: #fff;
    border-radius: 6px;
    border: 1px solid #dedede;
    padding: 35px 20px;
    margin-bottom: 30px;
    box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.15);
}

.form-data h2 {
    position: absolute;
    left: 20px;
    top: -17px;
    color: #0080AE;
    color: var(--bluish);
    font-weight: 600;
    background: transparent;
    font-size: 20px;
}

.form-row {
    display: grid;
    grid-template-columns: 1fr;
    position: relative;
}

.form-row-1-2 {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 20px;
    gap: 20px;
    position: relative;
}

.form-row-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    position: relative;
}

.form-row-2-1 {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    position: relative;
}

.form-row-2-1-1 {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    position: relative;
}

.form-row-3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    gap: 20px;
    position: relative;
}

@media screen and (max-width: 550px) {

    .form-row,
    .form-row-2,
    .form-row-2-1-1,
    .form-row-3,
    .form-row-4,
    .form-row-1-2 {
        grid-template-columns: 1fr;
    }
}

.input-group {
    display: block;
    position: relative;
    margin-bottom: 15px;
}

.input-group>label {
    font-weight: 400;
    font-size: 14px;
    color: #666;
    display: block;
    position: relative;
    margin-bottom: 5px;
    color: #808080;
    color: var(--lighter-text);
}

.form-control {
    display: block;
    outline: 0;
    border: 1px solid #ddd;
    padding: 10px 18px;
    width: 100%;
    border-radius: 3px;
    font-size: 14px;
    color: #666;
    font-family: Poppins;

}

.form-control input[type=tel] {
    display: block;
    outline: 0;
    border: none;
    padding: 10px 18px;
    width: 100%;
    border-radius: 3px;
    font-size: 16px;
    color: #888;
    font-family: Poppins;
    letter-spacing: 1px;
    font-weight: 500;
}

.input-group-doctor .form-control input[type=tel] {
    font-size: 14px;
}

.form-control input[type=tel]:focus {
    border: none;
    box-shadow: none;
}

select.form-control {
    width: 100%;
}

/* Buttons */
