    box-shadow: 0 0 0 var(--transparent-green);
}

.btn-main {
    position: relative;
    display: inline-block;
    border: 2px solid #0080AE;
    border: 2px solid var(--bluish);
    outline: none;
    padding: 5px 25px;
    background: #0080AE;
    background: var(--bluish);
    cursor: pointer;
    text-decoration: none;
    color: #fff;
    border-radius: 3px;
    font-family: Poppins;
}

.btn-main-outline {
    position: relative;
    display: inline-block;
    border: 2px solid #0080AE;
    border: 2px solid var(--bluish);
    outline: none;
    padding: 5px 25px;
    color: #0080AE;
    color: var(--bluish);
    cursor: pointer;
    text-decoration: none;
    background: #fff;
    border-radius: 3px;
    font-family: Poppins;
}

.btn-main:hover {
    background: #0080AEAA;
}

.btn-main-outline:hover {
    background: #0080AE33;
}

.link-main {
    position: relative;
    display: inline-block;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    text-decoration: underline;
    color: #0080AE;
    color: var(--bluish);
    font-family: Poppins;
}

.link-main:hover {
    opacity: 0.8;
}

/* Main body parts */
