    display: grid;
    grid-template-columns: 2fr 3fr;
    font-family: 'Poppins';
    z-index: 4;
    width: 80vw;
    margin: 0 auto;
}

@media screen and (max-width: 760px) {
    .body-container {
        grid-template-columns: 1fr;
    }

    .body-container .left-container {
        padding: 30px;
    }
}

.body-container .left-container,
.body-container .right-container {
    display: block;
    position: relative;
    background: #fff;
    padding: 40px;
}

.body-container .left-container img {
    width: 100%;
    max-width: 250px;
    display: block;
    position: relative;
    object-fit: cover;
    margin: 20px 0 40px 0;
    box-shadow: 3px 12px 16px rgba(0, 0, 0, 0.15);

}

/* Doctor's allow view */
.doctor-view {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background: #efefef;
    padding: 15px;
    margin-top: 30px;
}

.doctor-view>div>i {
    margin-right: 5px;
    color: #088939;
    color: var(--main-green);
    font-size: 18px;
}

.doctor-view>div>span {
    color: #555;
    font-size: 16px;
}

.doctor-view button {
